













import { Component, Vue, Watch } from 'vue-property-decorator';

interface ReportData {
  report: string;
  text: string;
}

@Component
export default class ReportTabs extends Vue {
  private categories?: string[] = ['常用', '全部', '走势', '路珠'];
  private categoryActive?: number = 0;
  private gameType?: string = 'pk10';
  private gameId?: string | number = 0;
  private types?: ReportData[][] = [ // NOTE type should change based on gameType
    [
      {
        report: 'basictable',
        text: '开奖号码'
      },
      {
        report: 'twosidesstatistics',
        text: '双面统计',
      },
      {
        report: 'longdragonstatics',
        text: '长龙统计',
      },
      {
        report: 'numberdistribution',
        text: '号码分布',
      },
      {
        report: 'numbertrend',
        text: '号码走势',
      },
      {
        report: 'hotandcold',
        text: '冷热分析',
      },
      {
        report: 'sumnumbers',
        text: '冠亚和走势',
      },
      {
        report: 'singledoublehistory',
        text: '单双大小历史',
      },
    ],
    [
      {
        report: 'singledoubleroadbeads',
        text: '单双大小路珠'
      },
      {
        report: 'twosideshistory',
        text: '冠亚和两面历史'
      },
      {
        report: 'locationtrend',
        text: '位置走势'
      },
      {
        report: 'todaynumberstatistics',
        text: '今日号码统计'
      }
      // {
      //   report: 'basictable',
      //   text: '号码分布'
      // },
      // '号码分布',
      // '号码规律统计'
    ],
    // [
    //   '开奖号码',
    //   '双面统计',
    //   '长龙统计',
    //   '号码分布',
    //   '号码走势',
    //   '路珠分析',
    //   '号码前后路珠',
    //   '冷热分析',
    //   '冠亚和路珠',
    //   '冠亚和走势',
    //   '单双大小历史',
    //   '龙虎统计',
    //   '龙虎路珠',
    //   '号码规律统计',
    //   '今日号码统计',
    //   '位置走势',
    //   '冠亚和两面历史',
    //   '单双大小路珠',
    // ],
    // [
    //   '号码走势',
    //   '冠亚和走势',
    //   '位置走势'
    // ],
    // [
    //   '路珠分析',
    //   '号码前后路珠',
    //   '冠亚和路珠',
    //   '龙虎路珠',
    //   '单双大小路珠'
    // ]
  ];
  private typeActive?: number = 0;
  public categoryChange = (catId: number) => {
    this.categoryActive = catId;
  }
  private typeChange = (tabId: number) => {
    const category = (this.types as ReportData[][])[this.categoryActive as number];
    const type = category[tabId as number];
    const params = {
      gametype: this.gameType,
      gameid: this.gameId
    } as any;
    
    const url = type.report as string;
    this.$router.push({name: url, params});
  }
  private mounted() {
    const route = this.$route;
    (this.types as ReportData[][])[1].forEach((item, index) => {
      if(item.report === route.name) {
        this.categoryActive = 1;
        this.typeActive = index;
      }
    })
  }
}
